.btn::before {
  content: "";
  border: 0.125rem solid #fff;
  border-radius: 3.3125rem;
  width: 98.2089552239%;
  height: 4rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
@media screen and (min-width: 768px) {
